import { getTimingFunction } from './patterns/timingFunctions';
import constants from "./constants";
import Random from "./prng";
export var retileGrid = function (grid, rows, cols) {
    var newGrid = [];
    for (var j = 0; j < rows; j++) {
        var newRow = [];
        var row = grid[j % grid.length];
        for (var i = 0; i < cols; i++) {
            newRow.push(row[i % row.length]);
        }
        newGrid.push(newRow);
    }
    return newGrid;
};
export var gridCopy = function (grid) { return grid.map(function (row) { return row.slice(); }); };
export var gridFlippedDiag = function (grid) {
    var rows = grid.length;
    var cols = grid[0].length;
    var newGrid = [];
    for (var j = 0; j < cols; j++) {
        var newRow = [];
        for (var i = 0; i < rows; i++) {
            newRow.push(grid[i][j]);
        }
        newGrid.push(newRow);
    }
    return newGrid;
};
export var getPatternsFromFrames = function (frames, g) {
    var patterns = [];
    var colors = constants.colors;
    frames.forEach(function (frame, n) {
        var lastGrid = n > 0 ? frames[n - 1].grid : undefined;
        var pattern = {
            getColor: function (col, row) {
                if (!frame.grid[row]) {
                    return colors[0];
                }
                var colorIdx = frame.grid[row][col];
                if (lastGrid && lastGrid[row][col] === colorIdx) {
                    return undefined;
                }
                else {
                    return constants.getColorVariation(colorIdx, row, g);
                }
            },
            fade: frame.fade,
            getOffset: getTimingFunction(frame.timingFunc),
            wait: frame.wait,
        };
        patterns.push(pattern);
    });
    return patterns;
};
var _offsetMultipliers = {
    center: 0.3,
    centerCorrect: -0.32,
    squirm: 0.4,
    squirmXCorrect: 1.01,
};
export var addPositionDistortion = function (x, y, triangleWidth, g) {
    if (g.distortType === 'center') {
        var midWidth = g.pageWidth / 2;
        var midHeight = g.pageHeight / 2;
        var xDist = midWidth - x;
        var yDist = midHeight - y;
        var maxMagnitude = Math.sqrt(Math.pow(midHeight, 2) + Math.pow(midWidth, 2));
        var magnitude = Math.sqrt(Math.pow(xDist, 2) + Math.pow(yDist, 2));
        var mult = magnitude / maxMagnitude;
        return {
            x: (x + xDist * mult * _offsetMultipliers.center) + xDist * _offsetMultipliers.centerCorrect,
            y: (y + yDist * mult * _offsetMultipliers.center) + yDist * _offsetMultipliers.centerCorrect,
        };
    }
    else if (g.distortType === 'squirm') {
        var rand = new Random(Math.floor(x * y));
        var xOffset = (rand.nextFloat() - 0.5) * triangleWidth * _offsetMultipliers.squirm;
        var yOffset = (rand.nextFloat() - 0.5) * triangleWidth * _offsetMultipliers.squirm;
        return {
            x: (x + xOffset) * _offsetMultipliers.squirmXCorrect,
            y: y + yOffset
        };
    }
    else if (g.distortType === 'rotate') {
        var midWidth = g.pageWidth / 2;
        var midHeight = g.pageHeight / 2;
        var xDist = midWidth - x;
        var yDist = midHeight - y;
        var maxMagnitude = Math.sqrt(Math.pow(midHeight, 2) + Math.pow(midWidth, 2));
        var magnitude = Math.sqrt(Math.pow(xDist, 2) + Math.pow(yDist, 2));
        var mult = magnitude / maxMagnitude;
        var angle = (mult > 0.55 ? 0 : (0.55 - mult)) + Math.PI;
        var cosine = Math.cos(angle);
        var sine = Math.sin(angle);
        return {
            x: (xDist * cosine - yDist * sine) + midWidth,
            y: (xDist * sine + yDist * cosine) + midHeight,
        };
    }
    else {
        return { x: x, y: y };
    }
};
